.allPage {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.titleBar {
  background-color: #065f27; /* Dark-Green color */
  text-align: center;
  width: 100vw;
  height: 8%
}

.title {
  margin-top: 8px;
  color: #fff;
}

.underPage {
  display: flex;
  align-items: flex-start;
  width: 100%;
  height: 100%;
}

.sidebar {
  width: 20%;
  background-color: #f4f4f4;
  border-radius: 10px;
}

.sidebar ul {
  justify-content: center;
}

.sidebar ul li {
  margin-bottom: 10px;
}

.sidebarButton {
  cursor: pointer;
  border: #f4f4f4;
  background-color: #f4f4f4
}

.content {
  width: 80%;
  padding-top: 20px;
}

.contentColumn {
  margin-left: auto; /* Center the box horizontally */
  margin-right: auto; /* Center the box horizontally */
  width: 50%;
}

.section {
  background-color: #4fab65;
  border-radius: 10px; /* Rounded corners */
  padding-top: 10px;
  padding-bottom: 5px;
  padding-left: 10px;
  margin-bottom: 10px; /* Add some spacing between questions */
  display: flex;
  flex-direction: column;
}

.sectionTitle {
  flex: 6;
  display: flex
}

.clearButton {
  flex: 1;
}

.questionContainer {
  display: flex;
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 10px;
}

.circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 10px;
}

.question {
  padding-right: 10px;
}

.answer {
  border-radius: 5px;
}